import { useState } from "react";
import ReactPaginate from "react-paginate";
import { Link } from "react-router-dom";
import slate from "../images/Slate.png";

export default function Specialite() {
  const items = [
    "Saucisson pur porc",
    "Saucisson aux noisettes",
    "Saucisson aux cpes",
    "Saucisson au beaufort",
    "Saucisson au Porc et Sanglier",
    "Saucisson aux noix",
    "Saucisson au fromage de chvre",
    "Saucisson au roquefort",
    "Saucisson aux figues",
    "Saucisson piment",
    "Saucisson au Porc et Cerf",
    "Saucisson au Porc et Taureau",
    "Saucisson aux olives",
    "Sauci'flette",
    "Saucisson aux myrtilles",
    "Saucisson au gnpi",
    "Saucisson herbes intrieures",
    "Saucisson au fenouil",
    "Saucisson  l'ail des ours",
    "Saucisson  l'ail",
    "Saucisson fum",
    "Saucisson cendr",
    "Saucisson enrob aux Saveurs Provenales",
    "Saucisson enrob aux herbes de Provence",
    "Saucisson enrob au poivre noir",
    "Saucisson enrob au piment fort concass",
  ];

  const [page, setPage] = useState(1);
  const limit = 4;
  const offset = (page - 1) * limit;
  let totalPage = Math.ceil(items.length / limit);

  let results = items.slice(offset, offset + limit);

  return (
    <section className="specialite">
      <div className="specialite-cover"></div>
      <div className="desc">
        <h1>Gamme spcialit</h1>
      </div>
      <img src={slate} alt="Carte menu ardoise" />
      <ul className="specialite_items">
        {results.map((result, i) => {
          return <li key={i}>{result}</li>;
        })}
      </ul>
      <Link to="/nos-saucissons/grignotage">
        <button>Voir le gamme grignotage</button>
      </Link>
      <ReactPaginate
        className="pagination"
        breakLabel="..."
        pageRangeDisplayed={3}
        marginPagesDisplayed={2}
        pageCount={totalPage}
        previousLabel={
          <span className="material-symbols-outlined">arrow_back_ios</span>
        }
        nextLabel={
          <span className="material-symbols-outlined">arrow_forward_ios</span>
        }
        onPageChange={(event) => {
          setPage(event.selected + 1);
        }}
      />
    </section>
  );
}
